/**
* Item Data tab
*/
import Store from './lib/store.js'
import data from './item-data/data/service-query'
import Intent from './item-data/intent'
import Model from './item-data/model'
import View from './item-data/view'

data.selected = []

let store = Store('item-data-test', data)
let state = store()
state.editing = {}
store(state)

const ROOT = document.querySelector('.js-data-view')

let intent = Intent(ROOT)
let model = Model(intent, store)
let view = View(model, ROOT)
