import most from 'most'
import Delegate from '../lib/delegate.js'
import closest from 'dom-closest'

let isEnter = (e) => e.keyCode === 13
let isESC = (e) => e.keyCode === 27
let getId = e => closest(e.target, '.js-row').getAttribute('data-id')

function getIntent (domNode) {
  let delegate = Delegate(domNode)

  let editCell = delegate('dblclick', '.js-data-cell')
      .map(e => {
        return {
          id: getId(e),
          attribute: e.target.getAttribute('data-attribute')
        }
      })

  let toggleSelect = delegate('click', '.js-row-checkbox')
      .map(e => {
        return { id: getId(e), selected: e.target.checked }
      })

  let editKeys = delegate('keyup', '.data-cell-edit')
  let editEnd = editKeys.filter(isESC)

  return {
    editCell,
    editEnd,
    toggleSelect
  }
}

export default getIntent


